import { useEffect, useState } from "react";
import firebase from "../firebase";
import { useNavigate } from "react-router-dom";

import "./join.scss";

function Join() {
  const navigate = useNavigate();

  const [UserId, setUserId] = useState("");
  const [UserPw1, setUserPw1] = useState("");
  const [UserPw2, setUserPw2] = useState("");
  const [UserName, setUserName] = useState("");
  const [UserNumber, setUserNumber] = useState("");

  const [Submit, setSubmit] = useState(false);

  const handleSubmit = async () => {
    if (!(UserName && UserPw1 && UserPw2 && UserId && UserNumber)) {
      return alert(" ");
    } else if (UserPw1 !== UserPw2) {
      return alert("  ");
    } else {
      alert(" .");
      navigate("/");
    }

    let createUser = await firebase
      .auth()
      .createUserWithEmailAndPassword(UserId, UserPw1);

    //  user    
    await createUser.user.updateProfile({
      displayName: UserName,
    });

    console.log(createUser.user);
  };

  return (
    <div className="join">
      <form onSubmit={handleSubmit}>
        <h3>
          
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
            <path d="M256 512c141.4 0 256-114.6 256-256S397.4 0 256 0S0 114.6 0 256S114.6 512 256 512z" />
          </svg>
        </h3>
        <input
          type="email"
          name="userId"
          id="userId"
          placeholder=""
          value={UserId}
          onChange={(e) => setUserId(e.target.value)}
        />
        <input
          type="password"
          name="userPw1"
          id="userPw1"
          placeholder=""
          value={UserPw1}
          onChange={(e) => setUserPw1(e.target.value)}
        />
        <input
          type="password"
          name="userPw2"
          id="userPw2"
          placeholder=" "
          value={UserPw2}
          onChange={(e) => setUserPw2(e.target.value)}
        />

        <h3>
          
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
            <path d="M256 512c141.4 0 256-114.6 256-256S397.4 0 256 0S0 114.6 0 256S114.6 512 256 512z" />
          </svg>
        </h3>
        <input
          type="text"
          name="userName"
          id="userName"
          placeholder="() "
          value={UserName}
          onChange={(e) => setUserName(e.target.value)}
        />
        <h3>
          
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
            <path d="M256 512c141.4 0 256-114.6 256-256S397.4 0 256 0S0 114.6 0 256S114.6 512 256 512z" />
          </svg>
        </h3>
        <input
          type="number"
          name="userNumber"
          id="userNumber"
          placeholder=""
          value={UserNumber}
          onChange={(e) => setUserNumber(e.target.value)}
        />

        <button type="submit" onClick={handleSubmit}>
          
        </button>
      </form>
    </div>
  );
}

export default Join;
