import data from "./DB/shopDataTest.json";
const { shopItems } = data;

export function setShopItems() {
  return shopItems;
}
// console.log(shopItems);

//     id 
export function getItemId(itemId) {
  return shopItems.find((shopItem) => shopItem.id === itemId);
}

