import { Link, NavLink } from "react-router-dom";
import "./login.scss";
import kakaoImg from "../assets/images/kakao_open.png";

function Login() {
  return (
    <div className="login">
      <input type="email" name="userId" id="userId" placeholder="" />
      <input type="password" name="userPw" id="userPw" placeholder="" />

      <div className="keepLogin">
        <input type="checkbox" name="keepLogin" id="keepLogin" />
        <label htmlFor="keepLogin"></label>
      </div>

      <button type="submit"></button>
      <div className="loginOthers">
        <Link to="/join"></Link>
        <Link to=""> &#183;  </Link>
      </div>
      <p></p>
      <NavLink to="" className="kakao">
        <img src={kakaoImg} alt="" />
         
      </NavLink>
      <p></p>
      <NavLink to="" className="noMember">
          
      </NavLink>
    </div>
  );
}

export default Login;
