import { Link } from "react-router-dom";
import "./mainJournal.scss";

const data = [
  {
    id: "j1",
    title: " 3     ",
    des: "        .",
    category: "News",
    date: "Winter 2022",
    imgUrl: "journal1.jpg",
  },
  {
    id: "j2",
    title: "SUSIE SALMON   .",
    des: "     .     .",
    category: "Team",
    date: "Winter 2022",
    imgUrl: "journal2.png",
  },
];
const path = process.env.PUBLIC_URL;

function MainJournal() {
  return (
    <div className="mainJournal">
      <h2>Journal</h2>
      <div className="sectionContainer">
        {data.map((item) => (
          <section key={item.id}>
            <Link to="">
              <img
                src={`${path}/images/main/${item.imgUrl}`}
                alt={item.title}
              />
            </Link>
            <div className="inner">
              <h3>{item.title}</h3>
              <p>{item.des}</p>
            </div>
            <div className="sub">
              <p>{item.category}</p>
              <p>{item.date}</p>
            </div>
          </section>
        ))}
      </div>
    </div>
  );
}

export default MainJournal;
